import React, { FC } from 'react';
import {
  CellEditProps,
  ClickEnum,
  cnpjFormatter,
  ColumnsProps,
  cpfFormatter,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OrderType,
  phoneWithDDDFormatter,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { CLASS_NAME, Responsavel } from '../../../../models/dtos/ger/responsavel';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'nome',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const ResponsavelSearchView: FC<SearchViewProps<Responsavel>> = props => {
  const columns: ColumnsProps[] = [
    {
      dataField: 'nome',
      editable: true,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Nome',
    },
    {
      dataField: 'cpf',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      formatter: cpfFormatter,
      sort: true,
      text: 'CPF',
    },
    {
      dataField: 'cnpj',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      formatter: cnpjFormatter,
      sort: true,
      text: 'CNPJ',
    },
    {
      dataField: 'cei',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'CEI',
    },
    {
      dataField: 'tipo',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Tipo',
    },
    {
      dataField: 'telefone',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      formatter: phoneWithDDDFormatter,
      sort: true,
      text: 'Telefone',
    },
    {
      dataField: 'email',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'E-mail',
    },
  ];
  return (
    <WindowComponent title="Manutenção de Responsável" wikiId={4398}>
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'ResponsavelEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default ResponsavelSearchView;
